*{box-sizing:border-box}body{font-family:'Open Sans', sans-serif;position:relative;background-image:url(images/power5.jpg);background-size:cover;background-attachment:fixed;background-position:center;background-repeat:no-repeat}.title{padding:30px;text-align:center;font-size:35px;background-color:#ffececb3}.main-content{position:absolute}.side{float:left;padding:20px;margin:200px 0 0 30px}.side ul{list-style-type:none;padding:0}.side ul li{margin:20px;cursor:pointer}.side ul li a{color:black}.side ul li button{color:#d69215;background-color:black}.modal{display:none;position:fixed;z-index:1;padding-top:100px;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.4)}.modal-content{background-color:#cca1a1;margin:auto;padding:20px;border:1px solid #ffc1c1;width:80%}.close{color:#5a5555;float:right;font-size:28px;font-weight:bold}.close:hover,.close:focus{color:#000;text-decoration:none;cursor:pointer}.content{float:left;padding:20px;display:block;background-color:#f5f5f54f;margin-top:100px;border-radius:10px;overflow:hidden;position:relative;left:30%}.power-plants{display:inline-block;width:auto;margin:10px}div[class^="plant"]{display:block;height:250px;width:350px;margin:10px;cursor:pointer;border:2px solid black}div[class^="plant"]:hover{transform:scale(1.5)}.plant-hydro{background-image:url(images/hydro.jpg)}.plant-solar{background-image:url(images/solar.jpg)}.plant-thermal{background-image:url(images/thermal.jpg)}.plant-wind{background-image:url(images/wind.jpg)}.main-content:after{content:"";display:table;clear:both}@media (max-width: 600px){.side,.content{width:100%;height:auto}}.power-content1{background-color:#afdff5}.power-content2{background-color:#ece17cf5}.power-content1,.power-content2{float:left;padding:40px;width:50%;display:block;border-radius:10px;overflow:scroll;position:absolute;left:26%}.power-content1 img,.power-content2 img{margin-left:20%}
